@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
  font-family: 'Roboto', sans-serif;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.img-nav{
    margin: 0;
    padding: 0;
    width: 9em;
}

.img-carousel{
    height: 80vh;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

@media only screen and (max-width: 600px) {
    .img-nav{
        width: 4em;
    }

    .img-carousel{
        height: auto;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
    }
    
}

.btn-blue{
    background-color: #C4C4C4;
    color: white;
}

.text-justify{
    text-align: justify;
}

.social{
    margin: 15px;
    padding: 0;
    border-radius: 50%;
}


/*FOOTER CONTENT*/
footer div div{
    font-size: small;
}

footer div div ul{
    list-style-type: none;
    justify-content: start;
    text-align: start;
}

footer div div ul li a{
    text-decoration: none;
    color: #545454;
}

footer div div ul li a:hover{
    text-decoration: none;
    color: #000000;
}

.large{
    width: 25em;
    margin: 0 0 0 0;
}

@media only screen and (min-width: 1500px){
    .large{        
        margin-right: 12em;
    }

}

.nav-btn{
    background: linear-gradient(270deg, rgba(25, 135, 252, 0.727), rgba(14, 130, 255, 0.809));
   transition: 1s;
}

.nav-btn:hover{
    background: linear-gradient(90deg, rgba(108, 179, 255, 0.809), rgba(14, 130, 255, 0.809));
}

/*CARD CONTENT*/

.card{
    overflow: hidden;
}

.card .img-fx{
    transition: .5s;
    transform: scale(1);
    filter:brightness(1)
}

.card .img-fx:hover{
    transform: scale(1.1);
    filter:brightness(1.2);
}
